import React from "react";
import logo from './logo_mobee.png';

const Accueil = () => {

return (
	<div className="App-Home">
		<img src={logo} className="App-logo" alt="logo" />
	<div>

	<h1></h1>

		<h5> Bienvenue chez Mobee Travel ! </h5>
		
	</div>
	</div>
	);
};

export default Accueil;


