
import DropdownButton from 'react-bootstrap/DropdownButton'
import React, { Component } from 'react'
import SplitButton from 'react-bootstrap/SplitButton'
import Dropdown from 'react-bootstrap/Dropdown'
import {Link} from "react-router-dom"
import Slider from 'react-rangeslider'
import 'react-rangeslider/lib/index.css'


const params = new URLSearchParams(location.search);


class Budget extends Component {

 constructor(props)
 {
 super(props)
 }

getParams()
{
  return params;
}


 modifSearch(pVal) 

 {
    params.set('bud', pVal);   
  console.log(params.toString());
 }



render (){
  return(

  <div>
	<div className="App-Budget">

	<h1 >Votre budget </h1> 
  </div>

<div className="App-Budget_select"> 
<br /> 
<div >
<h5 >Budget : </h5> 
  <input type="number" onChange={e => this.modifSearch(e.target.value,1)} defaultValue={this.getParams().get('bud')} ></input>
</div>
<br /> 

<br /> 
<br /> 
</div>

<br /> 

<Link to={{ pathname : '/Home', search : ""}}>
      <button type="button" className="Btn_retour_budget">Retour</button>
</Link>

</div>

	);
}
};



export default Budget;



