import React, { Component } from 'react'
import DropdownButton from 'react-bootstrap/DropdownButton'
import SplitButton from 'react-bootstrap/SplitButton'
import Dropdown from 'react-bootstrap/Dropdown'
import {Link} from "react-router-dom"
import budget from"./Budget"


 const bud = new budget();

class Destination extends Component {

 modifSearch(pVal,pIndex) 

 {
  bud.getParams().set('voy', pVal);
  console.log(bud.getParams().toString());
 }

render (){
  return (
    <div className="App-Destination">
    <h1 color="#FFFF">Votre destination :</h1>
    <br /> 
    <div className="App-Destination-select"> 
    <Dropdown>
  <Dropdown.Toggle variant="success">
    Destination
  </Dropdown.Toggle>

  <Dropdown.Menu>
    <Dropdown.Item onClick={()=> this.modifSearch("a")}> Mer </Dropdown.Item>
    <br />
    <Dropdown.Item onClick={()=> this.modifSearch("b")}> Montagne </Dropdown.Item>
    <br />
    <Dropdown.Item onClick={()=> this.modifSearch("c")}> Ville </Dropdown.Item>
  </Dropdown.Menu>
</Dropdown>
 <br />
 <br />

  <select  onChange={e => this.modifSearch(e.target.value,1)} defaultValue={bud.getParams().get('voy')}>
    <option >Voyageurs</option>
    <option >1</option>
    <option >2</option>
    <option >3</option>
    <option >4</option>
    <option >5</option>
    <option >6</option>
    <option >7</option>
    <option >8</option>
    <option >9</option>
    <option >10</option>
    <option >12</option>
    <option >15</option>
    <option >20</option>

  </select>

    <br />
    <br />
   
 </div>

    <Link to="/Home">
    <button type="button" className="Btn_retour_destination">Retour</button>
    </Link>
    </div>
    );
        }
};


export default Destination;


